
.imgsize {
  cursor: pointer;
}
@media screen and (min-width: 1501px) {
.imgsize {
    width: 230px;
    height: 350px;
}
.pad {
    padding-left: 220px;
    padding-right: 220px;
}
}

/*1100分辨率（大于960px，小于1199px）*/
@media screen and (min-width: 1200px) and (max-width: 1500px) {
.imgsize {
    width: 200px;
    height: 300px;
    cursor: pointer
}
.pad {
    padding-left: 150px;
    padding-right: 150px;
}
}

/*1100分辨率（大于960px，小于1199px）*/
@media screen and (min-width: 960px) and (max-width: 1199px) {
.imgsize {
    width: 220px;
    height: 300px;
    cursor: pointer
}
.pad {
    padding-left: 80px;
    padding-right: 80px;
}
}

/*880分辨率（大于768px，小于959px）*/
@media screen and (min-width: 768px) and (max-width: 959px) {
.imgsize {
    width: 200px;
    height: 250px;
    cursor: pointer
}
.pad {
    padding-left: 30px;
    padding-right: 30px;
}
}

/*720分辨率（大于480px，小于767px）*/
@media only screen and (min-width: 480px) and (max-width: 767px) {
.imgsize {
    width: 200px;
    height: 250px;
    cursor: pointer
}
.pad {
    padding-left: 40px;
    padding-right: 40px;
}
}

/*440分辨率以下（小于479px）*/
@media only screen and (max-width: 479px) {
.imgsize {
    width: 200px;
    height: 250px;
    cursor: pointer
}
.pad {
    padding-left: 30px;
    padding-right: 30px;
}
}





